.sidebar{
    flex: 1;
    border-right: 0.5px solid rgb(230, 227, 227);
    min-height: 100vh;
    .top{
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        
        .logo{
            font-size: 20px;
            font-weight: bold;
            color: #6439ff;
            position: absolute;
            left: 0;
            padding-left: 15px;
            bottom: 10px;
        
            
        }
    }
    hr{
        height: 0;
        border: 0.5px solid rgb(230, 227, 227);

    }
    .center{
        padding-left: 10px;
        ul{
            list-style: none;
            padding: 0;
            margin: 0;

            .title{
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }
            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover{
                    background: #ece8ff;
    
                }
                .icon{
                    font-size: 18px;
                    color: #7451f8;
                

                }
                span{
                    font-size: 13px;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;

                }
            }
           
        }
    }

    .bottom{
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1){
                background: whitesmoke;
            }

            &:nth-child(2){
                background: #333;
            }

            &:nth-child(3){
                background: darkblue;
            }

        }
    }
}